var suspend= false
import { Lightning, Utils } from 'wpe-lightning-sdk'
import { StringConstants } from '../constants/StringConstants'
import ThunderCalls from '../api/ThunderCalls'
import ThunderJS from '../api/thunderJS'
import ButtonItem from './ButtonItemList'
/**
 * @export
 * @class SideNavbar
 * @extends {Lightning.Component}
 */
export class SideNavbar extends Lightning.Component {
  static getFonts() {
    return [{ family: 'Bold', url: Utils.asset('fonts/DejaVuSans-Bold.ttf') }]
  }
  static _template() {
    return {
      Wrapper: {
        Home: {
          x: 60,
          text: {
            fontSize: 56,
            fontFace: 'Regular',
            textAlign: 'center',
            text: StringConstants.TITLE,
          },
        },
        SuspendText:{
          x:1370,
          y:5,
          mountX:1,
          text:{
            text:"Suspend mode Active",
            fontSize:20,
            fontStyle:'bold'
          },
          visible:false
        },
        Popup: {
          rect: true,
          x: 1310-50,
          y: 0,
          alpha: 0,
          color: 0xff000000,
          w: 610,
          h: 800,
          Popuptext: {
            x: 10,
            y: 110,
            text: {
              fontSize: 56,
              fontFace: 'Regular',
              textAlign: 'center',
              textColor: 0xffffffff,
              text: 'Options',
            },
          },
          ButtonListView: {
            type: Lightning.components.ListComponent,
            x: 10,
            y: 176,
            w: 600,
            h: 300,
            invertDirection: true,
            roll: true,
            itemSize: 50,
            horizontal: false,
          },
        },

        Diagnostics: {
          x: 1390,
          // y: -5,
          rect: true,
          color: 0xabbabbcf,
          w: 200,
          h: 55,
          Searchtext: {
            x: 10,
            y: 5,
            text: {
              // color:0xffffffff,
              fontSize: 36,
              fontFace: 'Regular',
              textAlign: 'center',
              text: 'Diagnostics',
            },
          },
        },
        Controls: {
          x: 1690,
          // y:5,
          rect: true,
          w: 180,
          h: 55,
          color: 0xabbabbcf,
          Searchtext: {
            x: 10,
            y: 5,
            text: {
              fontSize: 36,
              fontFace: 'Regular',
              textAlign: 'center',
              text: 'Controls',
            },
          },
        },
        
      },
    }
  }

  $textVisible(bool){
    suspend=bool
    this.tag('SuspendText').visible=bool
    this.fireAncestors('$checkBox',bool)
  }
  /**
   * @memberof SideNavbar
   * Init method.
   */
  _init() {
    var items = [
      {
        title: 'Suspend',
      },
      {
        title: 'Stop',
      },
      {
        title: 'Refresh',
      },
    ]

    this.tag('ButtonListView').items = items.map((info, index) => {
      return {
        //  w: total_w,
        // h: hh,
        // w_image: 60,
        // h_image: 60,
        //gap: gap,
        //y:index*50,
        type: ButtonItem,
        data: info,
      }
    })
    this.tag('ButtonListView').start()
    // this._setState('HomeState')
  }
  _getFocused() {
    this._setState('HomeState')
    //call from applist
  }
  /**
   * @memberof SideNavbar
   * Focus method.
   */
  _focus() {
    // this.tag('HorizontalBar').setSmooth('alpha', 0.3)
    // this.tag('Wrapper').alpha = 1
  }

  /**
   * @memberof SideNavbar
   * Unfocus method.
   */
  _unfocus() {
    //this.tag('HorizontalBar').setSmooth('alpha', 0)
    //this.tag('Wrapper').alpha = 0.5
  }

  /**
   * @static
   * @returns array of States
   * @memberof SideNavbar
   */
  static _states() {
    return [
      class ButtonListView extends this {
        _getFocused() {
          if (this.tag('ButtonListView').length) {
            return this.tag('ButtonListView').element
          }
        }
        _handleDown() {
          if (this.tag('ButtonListView').length - 1 != this.tag('ButtonListView').index) {
            //this.tag('ButtonListView').setNext()
          }
          return this.tag('ButtonListView').element
        }
        _handleUp() {
          if (0 != this.tag('ButtonListView').index) {
            this.tag('ButtonListView').setPrevious()
          }
          return this.tag('ButtonListView').element
        }
        _handleLeft() {
          this.tag('Popup').patch({ alpha: 0 })
          this._setState('HomeState')
        }
        // _handleUp() {
        //   this._setState('ImageListView')
        // }
      },
      class HomeState extends this {
        $enter() {
          this.tag('Diagnostics').color = 0xff00b3dc
        }
		    _getFocused() {
          return this.tag('Wrapper').Diagnostics
          //call from applist
        }

        _focus() {
          console.log(this.tag('Diagnostics'))
          //change color
          //this.tag('Diagnostics').color=0xabbabbcf
          this.tag('Diagnostics').color = 0xff00b3dc

          return this.tag('Diagnostics')
        }
        _handleEnter() {
          console.log('enter pressed homestate')
          var url = 'https://px-apps.sys.comcast.net/lightning_apps/diagnostics/dist/index.html'           
          var thunderCalls = new ThunderCalls()
          //this._zoomIn()

          thunderCalls.launchLightning(url)
        }

        _handleKey(event) {
          var thunderCalls = new ThunderCalls()
          if (event.keyCode == 77) {
            //this._zoomOut()
            const config = {
              host: '127.0.0.1',
              port: 9998,
              default: 1,
            }
            var thunder = ThunderJS(config)
            thunderCalls.suspendLightning()
            thunder
              .call('org.rdk.RDKShell', 'moveToFront', { client: 'ResidentApp' })
              .then(result => {
                console.error('firebolt moveToFront Success')
              })
            thunder
              .call('org.rdk.RDKShell', 'setFocus', { client: 'ResidentApp' })
              .then(result => {
                console.log('fb setFocus Success')
              })
              .catch(err => {
                console.log('Error', err)
              })
          }
        }

        _unfocus() {
          //this.tag('Diagnostics').color=0xff292929
          console.log('exit')
          this.tag('Diagnostics').color = 0xabbabbcf
        }
        $exit() {
          console.log('exitt called homestate')
          //this.tag('Diagnostics').color=0xff292929
          this.tag('Diagnostics').color = 0xabbabbcf
          //return this.tag('Wrapper').Diagnostics
        }
        // _handleBack() {

        //   this._setState('HomeState')
        // }
		_handleDown() {
          this.fireAncestors('$setGridViewState')
        }
        _handleRight() {
          //this.tag('Diagnostics').color=0xff292929

          this._setState('LiveTVState')
        }
      },
      class LiveTVState extends this {
        $enter() {
          this.tag('Controls').color = 0xff00b3dc
        }

        _getFocused() {
          // this.tag('Controls').color = 0xabbabbcf
          return this.tag('Wrapper').Controls

          //call from applist
        }
        _focus() {
          // console.log(this.tag('Diagnostics'))
          // //change color
          // this.tag('Controls').color = 0xabbabbcf
          // return this.tag('Controls')
          this.tag('Controls').color = 0xff00b3dc
        }
        _unfocus() {
          //this.tag('Controls').color = 0xff292929
          this.tag('Controls').color = 0xabbabbcf
        }
        _handleLeft() {
          console.log('handle left pressed')
          //this.tag('Controls').color=0xff292929
          //this.tag('Controls').color = 0xabbabbcf
          //this.tag('Diagnostics').color = 0xabbabbcf
          this.tag('Popup').patch({ alpha: 0 })
          this._setState('HomeState')
        }
        _handleBack() {
          console.log('handle back pressed')
          this._setState('HomeState')
        }
        _handleEnter() {
          console.log('handle enter called')
          this.tag('Popup').patch({ alpha: 1 })
          this._setState('ButtonListView')
        }
        $exit() {
          this.tag('Controls').color = 0xabbabbcf
        }
      },

      ,
      ,
    ]
  }
}

