import { Lightning, Utils } from 'wpe-lightning-sdk'
import BottomMenuItem from './BottomMenuItem'

/**
 * Bottom menu
 */
export default class BottomMenu extends Lightning.Component {
    static _template() {
        return {
            x: 0,
            y: 0,
            w: 1920,
            h: 400,
            rect: true,
            color: 0xff000000,
            List: {
                type: Lightning.components.ListComponent,
                x: 1920 / 2,
                y: 100,
                w: 800,
                h: 300,
                mountX: 0.5,
                roll: true,
                itemSize: 250,
            }
        }
    }

    /**
     * Set the items for bottom menu.
     */
    set info(info) {
        this.items = info
        this.tag('List').items = []
        this.tag('List').items = this.items.map((info, index) => {
            return {
                w: 200,
                h: 200,
                type: BottomMenuItem,
                item: info,
            }
        })
    }

    _getFocused() {
        console.log('list')
        return this.tag('List').element
    }

    _handleLeft() {
        return this.tag('List').setPrevious()
    }

    _handleRight() {
        return this.tag('List').setNext()
    }

    _handleEnter() {
        console.log(this.tag('List').element._item)
        this.fireAncestors('$selectedOption', this.tag('List').element._item)
    }
}