import { Lightning, Utils } from 'wpe-lightning-sdk'

/**
 * Class to render items in listings pane..
 */
export default class ButtonItem extends Lightning.Component {
  static _template() {
    return {
     Rect:{rect:true,
     x:0,
     y:10,
     w:600,
     h:50,
     color:0xffe6781e,
     alpha:0
    }
     ,
        Text: {
          x: 0,
          y: 10,
          text: { text: ' ', textColor: '0xffffffff', fontSize: 33,fontStyle:'bold' }
        },
        Icon:{
          x:550,
          y:20,
          mountX:1,
          //src:Utils.asset('images/icon-sidebar-stop.png')
        }

     }
  }
  _init() {
    console.log('&*&*&*&*&')
    //this.tag('Rect').patch({ color:0xffffffff})
    console.log(this.y)
    this.tag('Text').patch({ text: { text: this.data.title } })
    this.tag('Icon').src=Utils.asset('images/icon-sidebar-'+this.data.title.toLowerCase()+'.png')
    this.suspend=false

  }
  /**
   * Function to change properties of items during focus.
   */
  _focus() {
  //  this.tag('Text').patch({ text: { textColor: 0xffffffff} })
      this.tag('Rect').patch({ alpha:1})


  }

  /**
   * Function to change properties of items during unfocus.
   */
  _unfocus() {
    this.tag('Rect').patch({ alpha:0})
  //  this.tag('Text').patch({ text: { textColor: 0xfff5ec42} })

  }
  _handleEnter(){
    console.log("button Enter*****************")
    if(this.data.title=='Suspend'){
      this.tag('Icon').src=!this.suspend?Utils.asset('images/switch-on.png'):Utils.asset('images/icon-sidebar-'+this.data.title.toLowerCase()+'.png')
      this.suspend=!this.suspend
      this.fireAncestors('$textVisible',this.suspend)
    }
  }
}