
import { Lightning, Utils,Log } from 'wpe-lightning-sdk'
import AppList from './components/AppList.js'
import { Screen } from './constants/ScreenConstants'

export default class App extends Lightning.Component {
  static getFonts() {
    return [{ family: 'Regular', url: Utils.asset('fonts/Roboto-Regular.ttf') }]
  }

  static _template() {
    return {
      FullScreen: {
        w: Screen.width,
        h: Screen.height,
        rect: true,
        color: 0xff292929,
        AppList: {
          type: AppList,
          w: Screen.width,
          h: Screen.height - Screen.navBarHeight,
          x: 0,
          y: Screen.navBarHeight
        },
        alpha:1
      },
    }
  }

  _init() {
    Log.info('Firebolt App')
    this._setState('AppList')
  }

  static _states() {
    return [
      class AppList extends this {
        _getFocused() {
          return this.tag('FullScreen.AppList')
        }
      }
    ]
  }
}
