import ThunderJS from './thunderJS'
var activatedWeb = false
var activatedLightning = false
var activatedCobalt = false
var webUrl = ''
var lightningUrl = ''
var cobaltUrl = ''
const config = {
  host: '127.0.0.1',
  port: 9998,
  default: 1
}
var thunder = ThunderJS(config)
/**
 * Class that contains functions which commuicates with thunder API's
 */
export default class ThunderCalls {
  /**
   * Function to launch Html app.
   * @param {String} url url of app.
   */
  launchWeb(url) {
    if (!activatedWeb) {
      this.activateWeb(url)
    } else {
      this.updateWebUrl(url)
    }
  }

  /**
   * Function to activate html app.
   * @param {String} url url of app.
   */
  activateWeb(url) {
    const childCallsign = 'HtmlApp'
    thunder.Controller.activate({ callsign: childCallsign })
      .then(() => {
        activatedWeb = true
        thunder.call(childCallsign, 'state', 'resumed')
      })
      .then(result => {
        this.updateWebUrl(url)
      })
      .catch(err => {
        console.log('Error', err)
      })
  }

  /**
   * Function to update url of html app.
   * @param {String} url url of app.
   */
  updateWebUrl(url) {
    const childCallsign = 'HtmlApp'
    if (webUrl != url) {
      webUrl = url
      thunder.call(childCallsign, 'url', url)
    }
    thunder
      .call(childCallsign, 'state', 'resumed')
      .then(() => {
        thunder.call('org.rdk.RDKShell', 'moveToFront', { client: 'HtmlApp' })
      })
      .then(() => {
        thunder.call('org.rdk.RDKShell', 'setFocus', { client: 'HtmlApp' })
      })
      .catch(err => {
        console.log('Error' + JSON.stringify(err))
      })
  }

  /**
   * Function to launch Lightning app.
   * @param {String} url url of app.
   */
  launchLightning(url) {
    if (!activatedLightning) {
      this.activateLightning(url)
    } else {
      this.updateLightningUrl(url)
    }
  }

  /**
   * Function to activate lightning app.
   * @param {String} url url of app.
   */
  activateLightning(url) {
    const childCallsign = 'LightningApp'
    thunder.Controller.activate({ callsign: childCallsign })
      .then(result => {
        activatedLightning=true
        this.updateLightningUrl(url)
      })
      .catch(err => {
        console.log('Error', err)
      })
  }

  /**
   * Function to update url of lightning app.
   * @param {String} url url of app.
   */
  updateLightningUrl(url) {
    const childCallsign = 'LightningApp'
    if (lightningUrl != url) {
      lightningUrl = url
      thunder.call(childCallsign, 'url', url)
    }
    thunder
      .call(childCallsign, 'state', 'resumed')
      .then(() => {
        thunder.call('org.rdk.RDKShell', 'moveToFront', { client: 'LightningApp' })
      })
      .then(() => {
        thunder.call('org.rdk.RDKShell', 'setFocus', { client: 'LightningApp' })
      })
      .catch(err => {
        console.log('Error' + JSON.stringify(err))
      })
  }

  /**
   * Function to launch Cobalt app.
   * @param {String} url url of app.
   */
  launchCobalt(url) {
    console.log(activatedCobalt)
    if (!activatedCobalt) {
      this.activateCobalt(url)
    } else {
      this.updateCobaltUrl(url)
    }
  }

    /**
   * Function to activate cobalt app.
   * @param {String} url url of app.
   */
  activateCobalt(url) {
    const childCallsign = 'Cobalt'
    thunder.Controller.activate({ callsign: childCallsign })
      .then(() => {
        activatedCobalt = true
        thunder.call(childCallsign, 'state', 'resumed')
      })
      .then(result => {
        this.updateCobaltUrl(url)
      })
      .catch(err => {
        console.log('Error', err)
      })
  }

  /**
   * Function to update url of cobalt app.
   * @param {String} url url of app.
   */
  updateCobaltUrl(url) {
    const childCallsign = 'Cobalt'
    if (cobaltUrl != url) {
      cobaltUrl = url
      thunder.call(childCallsign, 'url', url)
    }
    thunder
      .call(childCallsign, 'state', 'resumed')
      .then(() => {
        thunder.call('org.rdk.RDKShell', 'moveToFront', { client: 'Cobalt' })
      })
      .then(() => {
        thunder.call('org.rdk.RDKShell', 'setFocus', { client: 'Cobalt' })
      })
      .catch(err => {
        console.log('Error' + JSON.stringify(err))
      })
  }

  /**
   * Function to suspend html app.
   */
  suspendWeb() {
    thunder.call('HtmlApp', 'state', 'suspended')
  }

  /**
   * Function to suspend lightning app.
   */
  suspendLightning() {
    thunder.call('LightningApp', 'state', 'suspended')
  }

    /**
   * Function to suspend cobalt app.
   */
  suspendCobalt() {
    thunder.call('Cobalt', 'state', 'suspended')
  }

  refresh(){
    //thunder.call('ResidentApp', 'url', '<url>')
  }

  /**
   * Function to deactivate html app.
   */
  deactivateWeb() {
    thunder.Controller.deactivate({ callsign: 'HtmlApp' })
    activatedWeb = false
    webUrl = ''
  }

  /**
   * Function to deactivate cobalt app.
   */
  deactivateCobalt() {
    thunder.Controller.deactivate({ callsign: 'Cobalt' })
    activatedCobalt = false
    cobaltUrl = ''
  }

  /**
   * Function to deactivate lightning app.
   */
  deactivateLightning() {
    thunder.Controller.deactivate({ callsign: 'LightningApp' })
    activatedLightning = false
    lightningUrl = ''
  }

  static pluginStatus(plugin){
    switch(plugin){
      case'WebApp': return activatedWeb    
      case 'Cobalt': return activatedCobalt
      case 'Lightning': return activatedLightning
    }
  }
}
